import axios from 'axios'

export const nuevaMascota = (mascota) => axios.post('http://localhost:8080/api/mascota/nuevo', mascota)

export const listarMascotas = () => axios.get('http://localhost:8080/api/mascotas')

export const buscarUnaMascota = (id) => axios.get(`http://localhost:8080/api/mascota/${id}`)

export const eliminarMascota = (id) => axios.delete(`http://localhost:8080/api/mascota/${id}`)

export const editarMascota = (id, mascota) => axios.put(`http://localhost:8080/api/mascota/${id}`, mascota)

export const ActualizarMascota = (id, mascota) => axios.put(`http://localhost:8080/api/mascota/${id}`, mascota)